import cx from "classnames"
import { Link } from "gatsby"
import React from "react"
import styled from "styled-components"

import Avatar from "@components/Avatar"
import useWindowScrollDirection from "@hooks/useWindowScrollDirection"

export default () => {
  const { isUp } = useWindowScrollDirection()
  return (
    <Header className={cx({ "hide": !isUp })}>
      <Container>
        <Title to="/">spriteye</Title>
        <Me to="/about" activeClassName="on-about" partiallyActive>
          <Avatar />
        </Me>
      </Container>
    </Header>
  )
}

const Header = styled.header`
  background-color: white;
  border-bottom: 1px solid #eee;
  position: sticky;
  display: flex;
  height: 3.5rem;
  top: 0;
  transition: top 0.4s ease;

  &.hide {
    top: -3.5rem;
  }
`

const Container = styled.div`
  margin: auto;
  padding: 0 2rem;
  width: 50rem;
  display: flex;
  justify-content: space-between;
`

const Title = styled(Link)`
  font-family: Monoton;
  font-size: 2rem;
  color: hotpink;
  align-items: center;
  display: flex;
`

const Me = styled(Link)`
  align-items: center;
  display: inline-flex;

  &.on-about {
    display: none;
  }
`
